*{
    margin: 0;
    font-family: 'Roboto', sans-serif;
  }

  :root{
    --main-color: #0b0b0b;
  }

  .link{
    color: inherit;
    text-decoration: none;
    white-space: nowrap;
  }

  @mixin tablet {
    @media(max-width:768px){
      @content;
    }
  }

  @mixin mobile {
    @media(max-width:480px){
      @content;
    }
  }

  @mixin smobile {
    @media(max-width:360px){
        @content;
    }
  }