@import "../../app.scss";

.register {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 1) 100%
    ),
    url("../../../public/image/homepage.jpg");
    position: relative;
    background-size: cover;


    .wrapper {
      padding: 20px 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
      z-index: 999;

      @include mobile {
        padding:20px 20px;
      }

      .logo {
        height: 40px;
      }

      .loginbutton{
        background-color: red;
        border: none;
        color:white;
        border-radius: 5px;
        padding: 5px 15px;
        font-size: 16px;
        font-weight: 500;
        cursor: pointer;
    }
  }
  .container{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;

    @include mobile {
      text-align: center;
    }

    h1{
        font-size: 50px;
    }

    h2{
        margin: 20px;
    }

    p{
        font-size: 20px;
    }
    

    .input{
        width: 50%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        margin-top: 20px;
        height: 50px;
        border-radius: 5px;

        @include mobile {
          width: 80%;
        }

        input{
            flex:9;
            width: 50%;
            background-color: white;
            height: 100%;
            border: none;
            padding: 15px;
            margin-top: 10px;

            
            @include mobile {
              width: 80%;
            }
        }

        .registerButton{
            flex: 3;
            height: 100%;
            background-color: red;
            border: none;
            color: white;
            font-size: 22px ;
            cursor: pointer;
            margin: 20px 0px;
            border-radius: 5px;
            padding: 15px 30px;

            @include mobile {
              font-size: 13px;
            }
        }
    }
  }
}
